import { ChangeEvent, useCallback, useState } from "react";

export const useInput = (initialValue: string = "") => {
  const [value, setValue] = useState(initialValue);

  const onChange = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    //—á—Ç–æ–±—ã —É–∑–Ω–∞–≤–∞—Ç—å —Å–∞–º–æ–µ —Å–≤–µ–∂–µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –∏—Å–ø —Ñ-—Ü–∏—é –æ–Ω—á—ç–π–Ω–¥–∂
    setValue(event.target.value); //—É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ—Ç —Å–∞–º–æ–µ —Å–≤–µ–∂–µ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
  }, []);

  return {
    value,
    onChange,
  };
};

// useCallback –≠—Ç–æ –æ–¥–∏–Ω –∏–∑ —Å–ø–æ—Å–æ–±–æ–≤ –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏–∏, –ø—Ä–∏–º–µ–Ω—è–µ–º—ã–π –¥–ª—è —É–≤–µ–ª–∏—á–µ–Ω–∏—è —Å–∫–æ—Ä–æ—Å—Ç–∏ –≤—ã–ø–æ–ª–Ω–µ–Ω–∏—è –∫–æ–º–ø—å—é—Ç–µ—Ä–Ω—ã—Ö –ø—Ä–æ–≥—Ä–∞–º–º. –ü–µ—Ä–µ–¥ –≤—ã–∑–æ–≤–æ–º —Ñ—É–Ω–∫—Ü–∏–∏ –ø—Ä–æ–≤–µ—Ä—è–µ—Ç—Å—è, –≤—ã–∑—ã–≤–∞–ª–∞—Å—å –ª–∏ —Ñ—É–Ω–∫—Ü–∏—è —Ä–∞–Ω–µ–µ: –µ—Å–ª–∏ –Ω–µ –≤—ã–∑—ã–≤–∞–ª–∞—Å—å,
//  —Ç–æ —Ñ—É–Ω–∫—Ü–∏—è –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è, –∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç –µ—ë –≤—ã–ø–æ–ª–Ω–µ–Ω–∏—è —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç—Å—è; –µ—Å–ª–∏ –≤—ã–∑—ã–≤–∞–ª–∞—Å—å, —Ç–æ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è —Å–æ—Ö—Ä–∞–Ω—ë–Ω–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç.
